
  ---
title: 
---
<template>
  <div>
    <my-container>
      <my-form :model="query"
               size="small"
               label-width="100px"
               :footer="null"
               inline
               @change="handleChange">
        <my-tag-select label=""
                       name="tag"
                       width="100%"
                       :options="tags"
                       :props="{multiple:true, checkAll:true, collapsible:true}"></my-tag-select>
        <my-select label=""
                   name="owner"
                   width="calc(50% - 20px)"
                   :loader="optionLoader"
                   :key-map="{label:'name',value:'id'}"
                   :props="{multiple:true,  collapseTags:true}"></my-select>
        <my-range label=""
                  name="date"
                  mode="date"
                  width="calc(50% - 20px)"
                  :props="{valueFormat:'yyyy-MM-dd'}"></my-range>
      </my-form>
    </my-container>

    <my-card-list ref="list"
                  class="card-list"
                  :loader="loader"
                  :columns="{xs:1,sm:1,md:2,lg:3,xl:4,xxl:6}"
                  pager>
      <el-card slot-scope="{item}"
               shadow="hover"
               :body-style="{padding:0}">
        <el-image :src="item.avatar" style="height: 180px; width:100%" fit="cover"></el-image>
        <my-container>
          <my-title :level="4">{{item.name}}</my-title>
          <my-paragraph ellipsis :rows="2">{{item.info}}</my-paragraph>
          <my-float>
            <my-float-item>
              <my-text type="secondary">{{item.date}}</my-text>
            </my-float-item>
            <my-float-item float="right">
              <el-link :underline="false"><i class="el-icon-star-off"></i>
                {{item.views}}
              </el-link>
            </my-float-item>
          </my-float>
        </my-container>
      </el-card>
    </my-card-list>

  </div>
</template>

<script>
  
     import __vue__ from 'vue';
     import {Card as __Card__,Image as __Image__,Link as __Link__} from 'element-ui';
     import {MyCardList as __MyCardList__,MyContainer as __MyContainer__,MyFloat as __MyFloat__,MyFloatItem as __MyFloatItem__,MyForm as __MyForm__,MyParagraph as __MyParagraph__,MyRange as __MyRange__,MySelect as __MySelect__,MyTagSelect as __MyTagSelect__,MyText as __MyText__,MyTitle as __MyTitle__} from '$ui';
     
     
     
     import MockForExample from '$my/code/mixin/mock-for-example'

  export default {
    mixins: [MockForExample],
    data() {
      return {
        tags: [],
        query: {},
        keyword: this.$route.query.keyword,
        list: []
      }
    },
    watch: {
      query() {
        this.$refs.list.refresh(1)
      },
      $route: {
        handler(val) {
          this.keyword = val.query.keyword
        }
      },
      keyword() {
        this.$refs.list.refresh(1)
      }
    },
    methods: {
      getTags() {
        const tags = []
        for (let i = 0; i < 20; i++) {
          tags.push(`${i + 1}`)
        }
        return tags
      },
      optionLoader() {
        return this.fetchMockForExample().then(res => {
          return res.list
        })
      },
      handleChange(query) {
        this.query = query
      },
      loader(page, limit) {
        return this.fetchMockForExample({
          data: {
            keyword: this.keyword,
            ...this.query,
            page,
            limit
          }
        })
      }
    },
    created() {
      this.tags = this.getTags()
    }
  }
     __vue__.use(__Card__);
__vue__.use(__Image__);
__vue__.use(__Link__)
     __vue__.use(__MyCardList__);
__vue__.use(__MyContainer__);
__vue__.use(__MyFloat__);
__vue__.use(__MyFloatItem__);
__vue__.use(__MyForm__);
__vue__.use(__MyParagraph__);
__vue__.use(__MyRange__);
__vue__.use(__MySelect__);
__vue__.use(__MyTagSelect__);
__vue__.use(__MyText__);
__vue__.use(__MyTitle__)
    
  </script>

<style lang="scss" scoped>
  .card-list {
    margin-top: 14px;
  }
</style>

  